import {RouterModule} from '@angular/router';
import {
  NotificacionesMainComponent,
  NotificacionesComponent,
  ListaPromocionesComponent
} from './'

const routes = [{
  path: '',
  component: NotificacionesMainComponent,
  children: [{
    path: 'panel',
    component: NotificacionesComponent
  }, {
    path: 'promociones',
    component: ListaPromocionesComponent
  }, {
    path: 'concurso',
    component: ListaPromocionesComponent
  }]
}]

export const NotificacionesRoutes = RouterModule.forChild(routes)



